@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;1,100&display=swap);
html body{padding:0;margin:0;font-family:"poppins" -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}.hero{visibility:hidden;height:100vh}.container{width:1440px;min-width:1440px;margin:0 auto}.hero-inner{display:flex;align-items:center;justify-content:space-between;margin:0 24px;height:100vh}.hero-content{width:50%}.hero-content-inner{width:400px;margin:0 auto;-webkit-columns:#323232;columns:#323232}h1{font-weight:300;font-size:32px;margin-bottom:24px}.hero-content-line{margin:0;height:44px;overflow:hidden}p{font-size:14px;line-height:24px;font-weight:302;padding-right:48px;margin-bottom:56px}.btn-row{position:relative}.btn-row::before{position:absolute;content:"";width:56px;height:1px;background-color:#dfdfbf;top:50%;left:-68px}button{background:none;border:none;font-size:12px;padding:0;cursor:pointer;text-transform:uppercase;font-weight:600;letter-spacing:0.7px;display:flex;align-items:center;position:relative}button:focus{outline:none}.arrow-icon{position:absolute;display:flex;align-items:center;justify-content:center;background:#007fff;height:42px;width:47px;left:100px;border-radius:100px}.hero-images{width:50%;height:100vh;position:relative}.hero-images-inner .hero-image{position:absolute;overflow:hidden}.hero-images-inner .hero-image.girl{top:0;right:0;width:45%;height:50%}.hero-images-inner .hero-image.boy{bottom:24px;left:0px;width:52%;height:65%}.hero-images-inner img{position:absolute;top:0;right:0;left:0;bottom:0;width:100%}

